:root {
    --primary-color: #0a0c36;
    --secondary-color: #9496c5;
    --thirtiary-color: #db97ea;
    --contrast-color: #d7d8f4;
}

#scrollToTop{
    z-index: 2;
    position: fixed;
    bottom: 3em;
    right: 4em;

    height: 50px;
    width: 50px;
    border: 1px solid var(--contrast-color);
    border-radius: 50%;
    
    background-color: var(--primary-color);
    color: var(--contrast-color);
    text-align: center;
}