:root{
    --primary-color: #0a0c36;
    --title-color: #080a34;
    --secondary-color: #9496c5;
    --thirtiary-color: #db97ea;
    --contrast-color: #d7d8f4;
}

.card{
    width: 25vw;
    height: 45vh;
    padding: 2em;
    margin: 2em 0;
    border: 1px solid white;
    border-radius: 10px;
    background-color: var(--secondary-color);
}

#type{
    margin-top: 1.5em;
    font-size: 1em;
    float: right;
}

.card img{
    width: 25vw;
}

.card h2{
    font-size: 1.5em;
    color: var(--title-color);
}

.card p{
    font-size: 1.2em;
    color: var(--contrast-color);
}